@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap);
html,
body {
  font-family: "Lato", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffffff;
  color: #414141;
}

body {
  margin: 0;
}

.btn {
  display: flex;
  align-items: center;
  height: 35px;
  padding: 5px 17px;
  border-radius: 5px;
  border: 1px solid transparent;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  outline: 0;
  cursor: pointer;
  transition: all 0.25s;
}
.btn:hover,
.btn:focus {
  opacity: 0.8;
}
.btn:active {
  opacity: 0.4;
}

.btn-secondary {
  background: #eef2f5;
  border-color: #eef2f5;
}
.btn-confirm {
  color: #fff;
  background: #10cd45;
  border-color: #10cd45;
}

.input-text {
  height: 35px;
  padding: 5px 17px;
  border-radius: 5px;
  border: 1px solid #eef2f5;
  font-size: 14px;
  line-height: 17px;
  outline: 0;
  transition: all 0.25s;
  box-sizing: border-box;
}
.input-text:hover,
.input-text:focus {
  border-color: #c6c9cc;
}

.form-error {
  font-size: 13px;
}

.close-modal {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 0;
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
}

.react-datepicker-wrapper {
  width: 100%;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.header{display:flex;justify-content:space-between;align-items:center}.header-control{display:flex;flex-direction:row}.header-control>button+button{margin-left:12px}.rc-pagination{display:flex;align-items:center;justify-content:center;padding:0}.rc-pagination li{list-style-type:none;outline:0}.rc-pagination-item{display:none}.rc-pagination-prev,.rc-pagination-next{cursor:pointer}.rc-pagination-prev{order:-1}.rc-pagination-prev svg{transform:scaleX(-1)}.rc-pagination-disabled{opacity:0.5;cursor:default}.rc-pagination-simple-pager input[type="text"]{display:inline-block;width:35px;height:35px;margin-right:19px;outline:0}.rc-pagination-total-text{margin:0 15px;display:flex;align-items:center}.rc-pagination-total-text-item{display:flex;width:35px;height:35px;align-items:center;justify-content:center;background:#eef2f5;border-radius:5px;border:1px solid #eef2f5;font-size:14px;line-height:17px;color:#414141}.rc-pagination-total-text-item:first-child{margin-right:19px}.rc-pagination-total-text-item:last-child{margin-left:19px}

.wrap{display:flex}.content{width:calc(100% - 250px);margin-left:250px;padding:55px 62px 15px 64px}

.aside{position:fixed;top:0;bottom:0;min-width:177px;padding:43px 40px 45px 33px;background:linear-gradient(175.93deg, #5f00d8 -10.23%, #b200f1 117.03%)}hr{margin-top:35px;margin-bottom:36px;opacity:0.2}

.menu{height:calc(100% - 110px)}nav{display:flex;flex-direction:column;height:100%}.menu-item{display:flex;font-weight:bold;font-size:16px;line-height:19px;color:#ffffff;cursor:pointer}.menu-item svg{margin-right:20px}.menu-item:not(:last-of-type){margin-bottom:40px}.menu-item:last-of-type{margin-top:auto}

.owners{min-height:700px}.owner{display:flex;justify-content:space-between;align-items:center;font-size:14px;line-height:17px}.owner:nth-child(even){background:rgba(238,242,245,0.4)}.owner:nth-child(1){color:#878787}.owner:nth-child(1)>div:nth-child(3){color:#878787}.owner:nth-child(1)>div:nth-child(4){color:#878787}.owner>div{display:flex;align-items:center;padding:15px 17px 16px 18px}.owner>div:nth-child(1){width:300px}.owner>div:nth-child(1) img{width:34px;height:34px;margin-right:23px}.owner>div:nth-child(2){width:120px}.owner>div:nth-child(3){width:120px;color:#00ca39}.owner>div:nth-child(4){width:120px;color:#e44067}.owner>div:nth-child(5){width:120px}

.add-owner_title{font-size:24px}.add-owner{display:flex;flex-direction:column;align-items:center}.add-owner form{display:block;width:100%}.add-owner__item:not(:last-child){margin-bottom:15px}.add-owner__item .btn,.add-owner__item .input-text{display:block;width:100%}

